/* ===== AUDIT PAGE STYLES ===== */

.audit-page {
  min-height: 100vh;
  background: var(--neutral-50);
  color: var(--neutral-900);
}

/* ===== AUDIT CONTENT ===== */
.audit-content {
  padding-top: 90px;
  max-width: 1200px;
  margin: 0 auto;
  padding-left: var(--space-lg);
  padding-right: var(--space-lg);
}

.audit-header {
  text-align: center;
  margin-bottom: var(--space-3xl);
  padding: var(--space-xl) 0;
}

.audit-title {
  font-size: var(--font-size-4xl);
  font-weight: var(--font-weight-bold);
  color: var(--neutral-900);
  margin-bottom: var(--space-sm);
}

.audit-subtitle {
  font-size: var(--font-size-lg);
  color: var(--neutral-600);
  line-height: var(--line-height-relaxed);
  max-width: 600px;
  margin: 0 auto;
}

/* ===== AUDIT FORM ===== */
.audit-form-container {
  max-width: 800px;
  margin: 0 auto var(--space-3xl);
}

.audit-form {
  background: white;
  border-radius: var(--radius-xl);
  border: 1px solid var(--neutral-200);
  box-shadow: var(--shadow-sm);
  padding: var(--space-2xl);
}

.form-section {
  margin-bottom: var(--space-2xl);
}

.section-title {
  font-size: var(--font-size-lg);
  font-weight: var(--font-weight-semibold);
  color: var(--neutral-900);
  margin-bottom: var(--space-sm);
}

.section-description {
  font-size: var(--font-size-sm);
  color: var(--neutral-600);
  margin-bottom: var(--space-lg);
  line-height: var(--line-height-relaxed);
}

/* ===== URL INPUT ===== */
.url-input-group {
  position: relative;
  display: flex;
  align-items: center;
}

.url-icon {
  position: absolute;
  left: var(--space-md);
  color: var(--neutral-400);
  z-index: 1;
}

.url-input {
  width: 100%;
  padding: var(--space-md) var(--space-md) var(--space-md) calc(var(--space-md) * 2 + 20px);
  font-size: var(--font-size-base);
  border: 2px solid var(--neutral-200);
  border-radius: var(--radius-lg);
  transition: all var(--transition-normal);
  background: white;
}

.url-input:focus {
  outline: none;
  border-color: var(--primary-500);
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.url-input:disabled {
  background: var(--neutral-100);
  cursor: not-allowed;
}

.error-message {
  color: var(--danger-600);
  font-size: var(--font-size-sm);
  margin-top: var(--space-sm);
  padding: var(--space-sm);
  background: var(--danger-50);
  border-radius: var(--radius-md);
  border: 1px solid var(--danger-200);
}

/* ===== AUDIT TYPES ===== */
.audit-types-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: var(--space-lg);
}

.audit-type-card {
  display: flex;
  align-items: center;
  gap: var(--space-md);
  padding: var(--space-lg);
  background: var(--neutral-50);
  border: 2px solid var(--neutral-200);
  border-radius: var(--radius-lg);
  cursor: pointer;
  transition: all var(--transition-normal);
  text-align: left;
  width: 100%;
  position: relative;
  overflow: hidden;
}

.audit-type-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: var(--accent-color);
  transform: scaleX(0);
  transition: transform var(--transition-normal);
}

.audit-type-card.selected::before {
  transform: scaleX(1);
}

.audit-type-card:hover {
  border-color: var(--accent-color);
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.audit-type-card.selected {
  border-color: var(--accent-color);
  background: var(--primary-50);
}

.audit-type-card:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.type-icon {
  width: 48px;
  height: 48px;
  background: var(--neutral-100);
  border-radius: var(--radius-lg);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--accent-color);
  flex-shrink: 0;
  transition: all var(--transition-normal);
}

.audit-type-card.selected .type-icon {
  background: var(--accent-color);
  color: white;
}

.type-content {
  flex: 1;
}

.type-label {
  font-size: var(--font-size-base);
  font-weight: var(--font-weight-semibold);
  color: var(--neutral-900);
  margin-bottom: var(--space-xs);
}

.type-description {
  font-size: var(--font-size-sm);
  color: var(--neutral-600);
  line-height: var(--line-height-relaxed);
}

.type-checkbox {
  flex-shrink: 0;
}

.checkbox {
  width: 24px;
  height: 24px;
  border: 2px solid var(--neutral-300);
  border-radius: var(--radius-md);
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all var(--transition-normal);
  background: white;
}

.checkbox.checked {
  border-color: var(--primary-500);
  background: var(--primary-500);
  color: white;
}

/* ===== FORM ACTIONS ===== */
.form-actions {
  text-align: center;
  margin-top: var(--space-2xl);
}

.loading-spinner {
  width: 20px;
  height: 20px;
  border: 2px solid transparent;
  border-top: 2px solid currentColor;
  border-radius: var(--radius-full);
  animation: spin 1s linear infinite;
  margin-right: var(--space-sm);
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

/* ===== AUDIT PROGRESS ===== */
.audit-progress-container {
  max-width: 800px;
  margin: 0 auto var(--space-3xl);
  background: white;
  border-radius: var(--radius-xl);
  border: 1px solid var(--neutral-200);
  box-shadow: var(--shadow-sm);
  padding: var(--space-2xl);
}

.progress-header {
  text-align: center;
  margin-bottom: var(--space-2xl);
}

.progress-title {
  font-size: var(--font-size-2xl);
  font-weight: var(--font-weight-bold);
  color: var(--neutral-900);
  margin-bottom: var(--space-sm);
}

.progress-subtitle {
  font-size: var(--font-size-base);
  color: var(--neutral-600);
}

.progress-bar-container {
  margin-bottom: var(--space-2xl);
}

.progress-bar {
  width: 100%;
  height: 12px;
  background: var(--neutral-200);
  border-radius: var(--radius-full);
  overflow: hidden;
  margin-bottom: var(--space-sm);
}

.progress-fill {
  height: 100%;
  border-radius: var(--radius-full);
  transition: width var(--transition-slow), background-color var(--transition-normal);
}

.progress-text {
  text-align: center;
  font-size: var(--font-size-lg);
  font-weight: var(--font-weight-semibold);
  color: var(--neutral-700);
}

/* ===== AUDIT STATUS GRID ===== */
.audit-status-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: var(--space-lg);
  margin-bottom: var(--space-2xl);
}

.status-card {
  display: flex;
  align-items: center;
  gap: var(--space-md);
  padding: var(--space-lg);
  background: var(--neutral-50);
  border-radius: var(--radius-lg);
  border: 1px solid var(--neutral-200);
  transition: all var(--transition-normal);
}

.status-card:hover {
  background: var(--neutral-100);
  border-color: var(--neutral-300);
}

.status-icon {
  flex-shrink: 0;
}

.status-content {
  flex: 1;
}

.status-title {
  font-size: var(--font-size-base);
  font-weight: var(--font-weight-semibold);
  color: var(--neutral-900);
  margin-bottom: var(--space-xs);
}

.status-description {
  font-size: var(--font-size-sm);
  color: var(--neutral-600);
  margin-bottom: var(--space-sm);
  line-height: var(--line-height-relaxed);
}

.status-badge {
  display: inline-block;
  padding: var(--space-xs) var(--space-sm);
  border-radius: var(--radius-md);
  font-size: var(--font-size-xs);
  font-weight: var(--font-weight-medium);
  color: white;
  text-transform: capitalize;
}

.progress-actions {
  display: flex;
  justify-content: center;
  gap: var(--space-md);
}

/* ===== AUDIT COMPLETED ===== */
.audit-completed-container {
  max-width: 800px;
  margin: 0 auto var(--space-3xl);
  background: white;
  border-radius: var(--radius-xl);
  border: 1px solid var(--neutral-200);
  box-shadow: var(--shadow-sm);
  padding: var(--space-2xl);
}

.completion-header {
  text-align: center;
  margin-bottom: var(--space-2xl);
}

.completion-icon {
  color: var(--success-500);
  margin-bottom: var(--space-lg);
}

.completion-title {
  font-size: var(--font-size-2xl);
  font-weight: var(--font-weight-bold);
  color: var(--neutral-900);
  margin-bottom: var(--space-sm);
}

.completion-subtitle {
  font-size: var(--font-size-base);
  color: var(--neutral-600);
  line-height: var(--line-height-relaxed);
}

.results-summary {
  margin-bottom: var(--space-2xl);
}

.summary-title {
  font-size: var(--font-size-lg);
  font-weight: var(--font-weight-semibold);
  color: var(--neutral-900);
  margin-bottom: var(--space-lg);
  text-align: center;
}

.summary-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: var(--space-lg);
}

.summary-card {
  display: flex;
  align-items: center;
  gap: var(--space-md);
  padding: var(--space-lg);
  background: var(--neutral-50);
  border-radius: var(--radius-lg);
  border: 1px solid var(--neutral-200);
  transition: all var(--transition-normal);
}

.summary-card:hover {
  background: var(--neutral-100);
  border-color: var(--neutral-300);
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.summary-icon {
  width: 40px;
  height: 40px;
  background: var(--primary-100);
  border-radius: var(--radius-lg);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--primary-600);
  flex-shrink: 0;
}

.summary-content {
  flex: 1;
}

.summary-label {
  font-size: var(--font-size-sm);
  font-weight: var(--font-weight-medium);
  color: var(--neutral-700);
  margin-bottom: var(--space-xs);
}

.summary-score {
  display: flex;
  align-items: center;
  gap: var(--space-sm);
}

.score-value {
  font-size: var(--font-size-lg);
  font-weight: var(--font-weight-bold);
  color: var(--neutral-900);
}

.score-grade {
  width: 24px;
  height: 24px;
  background: var(--primary-500);
  color: white;
  border-radius: var(--radius-full);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: var(--font-size-xs);
  font-weight: var(--font-weight-bold);
}

.completion-actions {
  text-align: center;
}

.secondary-actions {
  display: flex;
  justify-content: center;
  gap: var(--space-md);
  margin-top: var(--space-lg);
}

/* ===== CONNECTION STATUS ===== */
.connection-status {
  text-align: center;
  margin-top: var(--space-2xl);
}

.status-indicator {
  display: inline-flex;
  align-items: center;
  gap: var(--space-sm);
  padding: var(--space-sm) var(--space-md);
  background: var(--neutral-100);
  border-radius: var(--radius-lg);
  border: 1px solid var(--neutral-200);
}

.status-dot {
  width: 8px;
  height: 8px;
  border-radius: var(--radius-full);
  flex-shrink: 0;
}

.status-indicator.connected .status-dot {
  background: var(--success-500);
  box-shadow: 0 0 0 2px var(--success-100);
}

.status-indicator.disconnected .status-dot {
  background: var(--danger-500);
  box-shadow: 0 0 0 2px var(--danger-100);
}

.status-text {
  font-size: var(--font-size-sm);
  color: var(--neutral-600);
}

/* ===== RESPONSIVE DESIGN ===== */
@media (max-width: 768px) {
  .audit-content {
    padding-left: var(--space-md);
    padding-right: var(--space-md);
  }
  
  .audit-form,
  .audit-progress-container,
  .audit-completed-container {
    padding: var(--space-lg);
  }
  
  .audit-types-grid {
    grid-template-columns: 1fr;
  }
  
  .audit-status-grid {
    grid-template-columns: 1fr;
  }
  
  .summary-grid {
    grid-template-columns: 1fr;
  }
  
  .progress-actions {
    flex-direction: column;
    align-items: center;
  }
  
  .secondary-actions {
    flex-direction: column;
    align-items: center;
  }
}

@media (max-width: 480px) {
  .audit-title {
    font-size: var(--font-size-3xl);
  }
  
  .audit-type-card {
    flex-direction: column;
    text-align: center;
  }
  
  .type-checkbox {
    align-self: center;
  }
  
  .status-card {
    flex-direction: column;
    text-align: center;
  }
  
  .summary-card {
    flex-direction: column;
    text-align: center;
  }
}

/* ===== DARK THEME ADJUSTMENTS ===== */
[data-theme="dark"] .audit-page {
  background: var(--neutral-950);
}

[data-theme="dark"] .audit-form,
[data-theme="dark"] .audit-progress-container,
[data-theme="dark"] .audit-completed-container {
  background: var(--neutral-900);
  border-color: var(--neutral-800);
}

[data-theme="dark"] .audit-type-card {
  background: var(--neutral-800);
  border-color: var(--neutral-700);
}

[data-theme="dark"] .audit-type-card:hover {
  background: var(--neutral-700);
  border-color: var(--accent-color);
}

[data-theme="dark"] .audit-type-card.selected {
  background: var(--primary-900);
}

[data-theme="dark"] .status-card,
[data-theme="dark"] .summary-card {
  background: var(--neutral-800);
  border-color: var(--neutral-700);
}

[data-theme="dark"] .status-card:hover,
[data-theme="dark"] .summary-card:hover {
  background: var(--neutral-700);
  border-color: var(--neutral-600);
}

[data-theme="dark"] .url-input {
  background: var(--neutral-800);
  border-color: var(--neutral-700);
  color: var(--neutral-100);
}

[data-theme="dark"] .url-input:focus {
  border-color: var(--primary-400);
}

[data-theme="dark"] .url-input:disabled {
  background: var(--neutral-900);
}
