/* ===== REPORT PAGE STYLES ===== */

.report-page {
  min-height: 100vh;
  background: var(--neutral-50);
  color: var(--neutral-900);
}

/* ===== NAVIGATION ===== */
.navbar {
  background: var(--white);
  border-bottom: 1px solid var(--neutral-200);
  position: sticky;
  top: 0;
  z-index: var(--z-navbar);
  backdrop-filter: blur(10px);
  -webkit-backdrop-filter: blur(10px);
}

.nav-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 var(--space-lg);
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 70px;
}

.nav-brand {
  display: flex;
  align-items: center;
}

.brand-logo {
  display: flex;
  align-items: center;
  gap: var(--space-sm);
}

.logo-icon {
  width: 32px;
  height: 32px;
  color: var(--primary-500);
}

.brand-text {
  font-size: var(--text-xl);
  font-weight: var(--font-bold);
  color: var(--neutral-900);
}

.nav-menu {
  display: flex;
  align-items: center;
  gap: var(--space-xl);
}

.nav-link {
  color: var(--neutral-600);
  text-decoration: none;
  font-weight: var(--font-medium);
  transition: color var(--transition-fast);
  padding: var(--space-sm) var(--space-md);
  border-radius: var(--radius-md);
}

.nav-link:hover {
  color: var(--primary-600);
  background: var(--primary-50);
}

.theme-toggle {
  background: var(--neutral-100);
  border: none;
  padding: var(--space-sm);
  border-radius: var(--radius-md);
  color: var(--neutral-600);
  cursor: pointer;
  transition: all var(--transition-fast);
}

.theme-toggle:hover {
  background: var(--neutral-200);
  color: var(--neutral-800);
}

.nav-toggle {
  display: none;
  background: none;
  border: none;
  color: var(--neutral-600);
  cursor: pointer;
  padding: var(--space-sm);
}

/* ===== REPORT CONTENT ===== */
.report-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: var(--space-2xl) var(--space-lg);
}

/* ===== REPORT HEADER ===== */
.report-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: var(--space-2xl);
  padding: var(--space-xl);
  background: var(--white);
  border-radius: var(--radius-xl);
  box-shadow: var(--shadow-lg);
  border: 1px solid var(--neutral-200);
}

.header-left {
  display: flex;
  align-items: center;
  gap: var(--space-xl);
}

.back-button {
  display: flex;
  align-items: center;
  gap: var(--space-sm);
  background: var(--neutral-100);
  border: none;
  padding: var(--space-sm) var(--space-md);
  border-radius: var(--radius-md);
  color: var(--neutral-700);
  font-weight: var(--font-medium);
  cursor: pointer;
  transition: all var(--transition-fast);
}

.back-button:hover {
  background: var(--neutral-200);
  color: var(--neutral-900);
}

.header-info h1 {
  font-size: var(--text-3xl);
  font-weight: var(--font-bold);
  color: var(--neutral-900);
  margin-bottom: var(--space-xs);
}

.header-info p {
  color: var(--neutral-600);
  font-size: var(--text-lg);
}

.header-right {
  display: flex;
  align-items: center;
  gap: var(--space-xl);
}

.overall-score {
  text-align: center;
}

.score-circle {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background: var(--score-color);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: var(--space-sm);
  position: relative;
}

.score-circle::before {
  content: '';
  position: absolute;
  inset: -4px;
  border-radius: 50%;
  background: linear-gradient(45deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  animation: rotate 3s linear infinite;
}

@keyframes rotate {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}

.score-number {
  font-size: var(--text-xl);
  font-weight: var(--font-bold);
  color: var(--white);
  line-height: 1;
}

.score-grade {
  font-size: var(--text-sm);
  font-weight: var(--font-bold);
  color: var(--white);
  line-height: 1;
}

.score-label {
  font-size: var(--text-sm);
  color: var(--neutral-600);
  font-weight: var(--font-medium);
}

.header-actions {
  display: flex;
  gap: var(--space-sm);
}

/* ===== CATEGORY SCORES ===== */
.category-scores {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  gap: var(--space-2xl);
  margin-bottom: var(--space-2xl);
  max-width: 1400px;
  margin-left: auto;
  margin-right: auto;
}

.category-card {
  background: var(--white);
  border-radius: var(--radius-2xl);
  padding: var(--space-2xl);
  box-shadow: var(--shadow-lg);
  border: 1px solid var(--neutral-200);
  transition: all var(--transition-medium);
  position: relative;
  overflow: hidden;
  height: 100%;
  display: flex;
  flex-direction: column;
  min-height: 280px;
}

.category-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 6px;
  background: linear-gradient(90deg, var(--accent-color) 0%, transparent 100%);
}

.category-card::after {
  content: '';
  position: absolute;
  top: -2px;
  right: -2px;
  width: 60px;
  height: 60px;
  background: radial-gradient(circle, rgba(255,255,255,0.1) 0%, transparent 70%);
  border-radius: 50%;
  opacity: 0;
  transition: opacity var(--transition-medium);
}

.category-card:hover {
  transform: translateY(-8px);
  box-shadow: var(--shadow-2xl);
}

.category-card:hover::after {
  opacity: 1;
}

.category-header {
  display: flex;
  align-items: flex-start;
  gap: var(--space-xl);
  margin-bottom: var(--space-xl);
  flex: 1;
}

.category-icon {
  width: 64px;
  height: 64px;
  background: linear-gradient(135deg, var(--accent-color), rgba(255,255,255,0.1));
  border-radius: var(--radius-xl);
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--white);
  box-shadow: var(--shadow-md);
  position: relative;
  flex-shrink: 0;
}

.category-icon::before {
  content: '';
  position: absolute;
  inset: -2px;
  background: linear-gradient(135deg, var(--accent-color), transparent);
  border-radius: var(--radius-xl);
  z-index: -1;
  opacity: 0.3;
}

.category-info {
  flex: 1;
  min-width: 0;
}

.category-name {
  font-size: var(--text-2xl);
  font-weight: var(--font-bold);
  color: var(--neutral-900);
  margin-bottom: var(--space-sm);
  line-height: 1.2;
}

.category-description {
  color: var(--neutral-600);
  font-size: var(--text-md);
  line-height: 1.5;
}

.category-score {
  text-align: center;
  position: relative;
}

.score-display {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: var(--space-sm);
  position: relative;
}

.score-value {
  font-size: var(--text-4xl);
  font-weight: var(--font-bold);
  color: var(--accent-color);
  line-height: 1;
  position: relative;
}

.score-value::after {
  content: '';
  position: absolute;
  bottom: -4px;
  left: 50%;
  transform: translateX(-50%);
  width: 60%;
  height: 2px;
  background: var(--accent-color);
  border-radius: 1px;
  opacity: 0.3;
}

.grade-badge {
  padding: var(--space-sm) var(--space-md);
  border-radius: var(--radius-lg);
  font-size: var(--text-lg);
  font-weight: var(--font-bold);
  min-width: 48px;
  text-align: center;
  box-shadow: var(--shadow-sm);
  border: 2px solid rgba(255,255,255,0.8);
}

.category-metrics {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: var(--space-lg);
  padding-top: var(--space-xl);
  border-top: 2px solid var(--neutral-100);
  margin-top: var(--space-lg);
}

.metric {
  text-align: center;
  padding: var(--space-md);
  background: var(--neutral-50);
  border-radius: var(--radius-lg);
  border: 1px solid var(--neutral-200);
  transition: all var(--transition-fast);
  position: relative;
  overflow: hidden;
}

.metric::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: var(--accent-color);
  opacity: 0.5;
}

.metric:hover {
  background: var(--white);
  box-shadow: var(--shadow-md);
  transform: translateY(-2px);
}

.metric-label {
  display: block;
  font-size: var(--text-xs);
  color: var(--neutral-500);
  margin-bottom: var(--space-sm);
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: var(--font-semibold);
}

.metric-value {
  font-size: var(--text-xl);
  font-weight: var(--font-bold);
  color: var(--neutral-900);
  line-height: 1;
}

.priority-high {
  color: var(--danger-600);
  background: var(--danger-50);
  padding: var(--space-xs) var(--space-sm);
  border-radius: var(--radius-md);
  font-size: var(--text-sm);
  font-weight: var(--font-bold);
}

/* Score animation */
@keyframes scoreCount {
  from { opacity: 0; transform: translateY(10px); }
  to { opacity: 1; transform: translateY(0); }
}

.score-value {
  animation: scoreCount 0.8s ease-out 0.3s both;
}

.grade-badge {
  animation: scoreCount 0.8s ease-out 0.5s both;
}

/* Metric cards stagger animation */
.metric:nth-child(1) { animation: slideUp 0.6s ease-out 0.2s both; }
.metric:nth-child(2) { animation: slideUp 0.6s ease-out 0.4s both; }
.metric:nth-child(3) { animation: slideUp 0.6s ease-out 0.6s both; }

@keyframes slideUp {
  from { 
    opacity: 0; 
    transform: translateY(20px); 
  }
  to { 
    opacity: 1; 
    transform: translateY(0); 
  }
}

/* Card entrance animation */
.category-card {
  animation: cardEntrance 0.8s ease-out both;
}

.category-card:nth-child(1) { animation-delay: 0.1s; }
.category-card:nth-child(2) { animation-delay: 0.2s; }
.category-card:nth-child(3) { animation-delay: 0.3s; }
.category-card:nth-child(4) { animation-delay: 0.4s; }

@keyframes cardEntrance {
  from {
    opacity: 0;
    transform: translateY(30px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

/* Enhanced icon animation */
.category-icon {
  animation: iconPulse 2s ease-in-out infinite;
}

@keyframes iconPulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.05); }
}

/* ===== REPORT TABS ===== */
.report-tabs {
  display: flex;
  background: var(--white);
  border-radius: var(--radius-xl);
  padding: var(--space-sm);
  box-shadow: var(--shadow-lg);
  border: 1px solid var(--neutral-200);
  margin-bottom: var(--space-2xl);
  overflow: hidden;
}

.tab-button {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: var(--space-sm);
  padding: var(--space-md) var(--space-lg);
  background: transparent;
  border: none;
  color: var(--neutral-600);
  font-weight: var(--font-medium);
  cursor: pointer;
  transition: all var(--transition-fast);
  border-radius: var(--radius-lg);
}

.tab-button:hover {
  color: var(--primary-600);
  background: var(--primary-50);
}

.tab-button.active {
  background: var(--primary-500);
  color: var(--white);
  box-shadow: var(--shadow-md);
}

/* ===== TAB CONTENT ===== */
.tab-content {
  background: var(--white);
  border-radius: var(--radius-xl);
  padding: var(--space-2xl);
  box-shadow: var(--shadow-lg);
  border: 1px solid var(--neutral-200);
}

/* ===== OVERVIEW TAB ===== */
.overview-content {
  animation: fadeIn 0.5s ease-out;
}

.overview-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: var(--space-xl);
}

.overview-card {
  padding: var(--space-xl);
  background: var(--neutral-50);
  border-radius: var(--radius-lg);
  border: 1px solid var(--neutral-200);
}

.overview-card h3 {
  font-size: var(--text-xl);
  font-weight: var(--font-bold);
  color: var(--neutral-900);
  margin-bottom: var(--space-md);
  display: flex;
  align-items: center;
  gap: var(--space-sm);
}

.overview-card p {
  color: var(--neutral-700);
  line-height: 1.6;
  margin-bottom: var(--space-lg);
}

.findings-list {
  list-style: none;
  padding: 0;
}

.finding-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: var(--space-md);
  background: var(--white);
  border-radius: var(--radius-md);
  margin-bottom: var(--space-sm);
  border: 1px solid var(--neutral-200);
}

.finding-category {
  font-weight: var(--font-medium);
  color: var(--neutral-700);
}

.finding-status {
  font-weight: var(--font-bold);
  font-size: var(--text-sm);
}

.next-steps {
  display: flex;
  flex-direction: column;
  gap: var(--space-md);
}

.step-item {
  display: flex;
  align-items: flex-start;
  gap: var(--space-md);
}

.step-number {
  width: 32px;
  height: 32px;
  background: var(--primary-500);
  color: var(--white);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: var(--font-bold);
  flex-shrink: 0;
}

.step-content h4 {
  font-size: var(--text-md);
  font-weight: var(--font-semibold);
  color: var(--neutral-900);
  margin-bottom: var(--space-xs);
}

.step-content p {
  color: var(--neutral-600);
  font-size: var(--text-sm);
  line-height: 1.5;
}

/* ===== DETAILS TAB ===== */
.details-content {
  animation: fadeIn 0.5s ease-out;
}

.detail-section {
  margin-bottom: var(--space-xl);
  border: 1px solid var(--neutral-200);
  border-radius: var(--radius-lg);
  overflow: hidden;
}

.section-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: var(--space-lg);
  background: var(--neutral-50);
  cursor: pointer;
  transition: background var(--transition-fast);
}

.section-header:hover {
  background: var(--neutral-100);
}

.section-title {
  display: flex;
  align-items: center;
  gap: var(--space-md);
}

.section-title h3 {
  font-size: var(--text-xl);
  font-weight: var(--font-bold);
  color: var(--neutral-900);
}

.section-score {
  display: flex;
  align-items: center;
  gap: var(--space-md);
}

.section-score .score {
  font-size: var(--text-lg);
  font-weight: var(--font-bold);
  color: var(--neutral-700);
}

.section-score .grade {
  padding: var(--space-xs) var(--space-sm);
  border-radius: var(--radius-md);
  font-size: var(--text-sm);
  font-weight: var(--font-bold);
  min-width: 32px;
  text-align: center;
}

.section-content {
  padding: var(--space-xl);
  background: var(--white);
  border-top: 1px solid var(--neutral-200);
}

.content-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: var(--space-xl);
}

.content-card {
  padding: var(--space-lg);
  background: var(--neutral-50);
  border-radius: var(--radius-lg);
  border: 1px solid var(--neutral-200);
}

.content-card h4 {
  font-size: var(--text-lg);
  font-weight: var(--font-semibold);
  color: var(--neutral-900);
  margin-bottom: var(--space-md);
}

.metrics-list {
  display: flex;
  flex-direction: column;
  gap: var(--space-md);
}

.metric-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: var(--space-sm);
  background: var(--white);
  border-radius: var(--radius-md);
  border: 1px solid var(--neutral-200);
}

.metric-name {
  color: var(--neutral-600);
  font-size: var(--text-sm);
}

.metric-value {
  font-weight: var(--font-semibold);
  color: var(--neutral-900);
}

.technical-details {
  background: var(--neutral-900);
  border-radius: var(--radius-md);
  padding: var(--space-md);
  overflow: auto;
  max-height: 300px;
}

.json-display {
  color: var(--neutral-100);
  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;
  font-size: var(--text-xs);
  line-height: 1.4;
  margin: 0;
}

/* ===== ISSUES TAB ===== */
.issues-content {
  animation: fadeIn 0.5s ease-out;
}

.issues-header {
  text-align: center;
  margin-bottom: var(--space-2xl);
}

.issues-header h3 {
  font-size: var(--text-2xl);
  font-weight: var(--font-bold);
  color: var(--neutral-900);
  margin-bottom: var(--space-sm);
}

.issues-header p {
  color: var(--neutral-600);
  font-size: var(--text-lg);
}

.issues-summary {
  display: flex;
  flex-direction: column;
  gap: var(--space-xl);
}

.category-issues {
  border: 1px solid var(--neutral-200);
  border-radius: var(--radius-lg);
  overflow: hidden;
}

.category-header {
  display: flex;
  align-items: center;
  gap: var(--space-md);
  padding: var(--space-lg);
  background: var(--neutral-50);
  border-bottom: 1px solid var(--neutral-200);
}

.category-header h4 {
  font-size: var(--text-xl);
  font-weight: var(--font-bold);
  color: var(--neutral-900);
  flex: 1;
}

.issue-count {
  background: var(--primary-100);
  color: var(--primary-700);
  padding: var(--space-xs) var(--space-sm);
  border-radius: var(--radius-md);
  font-size: var(--text-sm);
  font-weight: var(--font-medium);
}

.issues-list {
  padding: var(--space-lg);
}

.issue-item {
  padding: var(--space-lg);
  border: 1px solid var(--neutral-200);
  border-radius: var(--radius-lg);
  margin-bottom: var(--space-md);
  background: var(--white);
  transition: all var(--transition-fast);
}

.issue-item:hover {
  box-shadow: var(--shadow-md);
  border-color: var(--primary-300);
}

.issue-header {
  display: flex;
  align-items: center;
  gap: var(--space-md);
  margin-bottom: var(--space-md);
}

.issue-priority {
  width: 12px;
  height: 12px;
  border-radius: 50%;
}

.issue-priority.high {
  background: var(--danger-500);
}

.issue-priority.medium {
  background: var(--warning-500);
}

.issue-priority.low {
  background: var(--success-500);
}

.issue-title {
  font-size: var(--text-lg);
  font-weight: var(--font-semibold);
  color: var(--neutral-900);
  flex: 1;
}

.issue-category {
  background: var(--neutral-100);
  color: var(--neutral-700);
  padding: var(--space-xs) var(--space-sm);
  border-radius: var(--radius-md);
  font-size: var(--text-xs);
  font-weight: var(--font-medium);
  text-transform: uppercase;
}

.issue-description {
  color: var(--neutral-700);
  line-height: 1.6;
  margin-bottom: var(--space-md);
}

.issue-suggestion {
  background: var(--primary-50);
  border: 1px solid var(--primary-200);
  padding: var(--space-md);
  border-radius: var(--radius-md);
  color: var(--primary-800);
  font-size: var(--text-sm);
}

/* ===== RECOMMENDATIONS TAB ===== */
.recommendations-content {
  animation: fadeIn 0.5s ease-out;
}

.recommendations-header {
  text-align: center;
  margin-bottom: var(--space-2xl);
}

.recommendations-header h3 {
  font-size: var(--text-2xl);
  font-weight: var(--font-bold);
  color: var(--neutral-900);
  margin-bottom: var(--space-sm);
}

.recommendations-header p {
  color: var(--neutral-600);
  font-size: var(--text-lg);
}

.recommendations-grid {
  display: flex;
  flex-direction: column;
  gap: var(--space-xl);
}

.category-recommendations {
  border: 1px solid var(--neutral-200);
  border-radius: var(--radius-lg);
  overflow: hidden;
}

.recommendations-list {
  padding: var(--space-lg);
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  gap: var(--space-lg);
}

.recommendation-item {
  padding: var(--space-lg);
  border: 1px solid var(--neutral-200);
  border-radius: var(--radius-lg);
  background: var(--white);
  transition: all var(--transition-fast);
}

.recommendation-item:hover {
  box-shadow: var(--shadow-md);
  border-color: var(--success-300);
}

.rec-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: var(--space-md);
}

.rec-title {
  font-size: var(--text-lg);
  font-weight: var(--font-semibold);
  color: var(--neutral-900);
  flex: 1;
}

.rec-priority {
  padding: var(--space-xs) var(--space-sm);
  border-radius: var(--radius-md);
  font-size: var(--text-xs);
  font-weight: var(--font-bold);
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.rec-priority.high {
  background: var(--danger-100);
  color: var(--danger-700);
}

.rec-priority.medium {
  background: var(--warning-100);
  color: var(--warning-700);
}

.rec-priority.low {
  background: var(--success-100);
  color: var(--success-700);
}

.rec-description {
  color: var(--neutral-700);
  line-height: 1.6;
  margin-bottom: var(--space-md);
}

.rec-impact,
.rec-effort {
  background: var(--neutral-50);
  padding: var(--space-sm);
  border-radius: var(--radius-md);
  margin-bottom: var(--space-sm);
  font-size: var(--text-sm);
}

.rec-impact strong,
.rec-effort strong {
  color: var(--neutral-900);
  margin-right: var(--space-sm);
}

/* ===== LOADING STATES ===== */
.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 400px;
  text-align: center;
}

.loading-spinner {
  width: 48px;
  height: 48px;
  border: 4px solid var(--neutral-200);
  border-top: 4px solid var(--primary-500);
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: var(--space-lg);
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.loading-container h2 {
  font-size: var(--text-2xl);
  font-weight: var(--font-bold);
  color: var(--neutral-900);
  margin-bottom: var(--space-sm);
}

.loading-container p {
  color: var(--neutral-600);
  font-size: var(--text-lg);
}

/* ===== RESPONSIVE DESIGN ===== */
@media (max-width: 1024px) {
  .report-content {
    padding: var(--space-xl) var(--space-md);
  }
  
  .report-header {
    flex-direction: column;
    gap: var(--space-lg);
    text-align: center;
  }
  
  .header-left {
    flex-direction: column;
    gap: var(--space-lg);
  }
  
  .header-right {
    flex-direction: column;
    gap: var(--space-lg);
  }
  
  .category-scores {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap: var(--space-lg);
  }
  
  .overview-grid {
    grid-template-columns: 1fr;
  }
  
  .content-grid {
    grid-template-columns: 1fr;
  }
  
  .recommendations-list {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 768px) {
  .nav-menu {
    position: fixed;
    top: 70px;
    left: 0;
    right: 0;
    background: var(--white);
    border-top: 1px solid var(--neutral-200);
    flex-direction: column;
    padding: var(--space-lg);
    transform: translateY(-100%);
    opacity: 0;
    transition: all var(--transition-fast);
  }
  
  .nav-menu.active {
    transform: translateY(0);
    opacity: 1;
  }
  
  .nav-toggle {
    display: block;
  }
  
  .report-tabs {
    flex-direction: column;
  }
  
  .tab-button {
    justify-content: flex-start;
  }
  
  .tab-content {
    padding: var(--space-lg);
  }
  
  .category-header {
    flex-direction: column;
    text-align: center;
    gap: var(--space-md);
  }
  
  .category-scores {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(4, auto);
    gap: var(--space-lg);
  }
  
  .category-metrics {
    grid-template-columns: 1fr;
    gap: var(--space-sm);
  }
}

@media (max-width: 480px) {
  .report-content {
    padding: var(--space-lg) var(--space-sm);
  }
  
  .report-header {
    padding: var(--space-lg);
  }
  
  .header-info h1 {
    font-size: var(--text-2xl);
  }
  
  .score-circle {
    width: 60px;
    height: 60px;
  }
  
  .score-number {
    font-size: var(--text-lg);
  }
  
  .category-card {
    padding: var(--space-lg);
  }
  
  .tab-content {
    padding: var(--space-md);
  }
}

/* ===== DARK THEME ADJUSTMENTS ===== */
[data-theme="dark"] .report-page {
  background: var(--neutral-900);
  color: var(--neutral-100);
}

[data-theme="dark"] .navbar {
  background: var(--neutral-800);
  border-bottom-color: var(--neutral-700);
}

[data-theme="dark"] .brand-text {
  color: var(--neutral-100);
}

[data-theme="dark"] .nav-link {
  color: var(--neutral-300);
}

[data-theme="dark"] .nav-link:hover {
  color: var(--primary-400);
  background: var(--primary-900);
}

[data-theme="dark"] .theme-toggle {
  background: var(--neutral-700);
  color: var(--neutral-300);
}

[data-theme="dark"] .theme-toggle:hover {
  background: var(--neutral-600);
  color: var(--neutral-200);
}

[data-theme="dark"] .report-header,
[data-theme="dark"] .category-card,
[data-theme="dark"] .report-tabs,
[data-theme="dark"] .tab-content {
  background: var(--neutral-800);
  border-color: var(--neutral-700);
}

[data-theme="dark"] .overview-card,
[data-theme="dark"] .content-card {
  background: var(--neutral-700);
  border-color: var(--neutral-600);
}

[data-theme="dark"] .section-header {
  background: var(--neutral-700);
}

[data-theme="dark"] .section-content {
  background: var(--neutral-800);
}

[data-theme="dark"] .category-header,
[data-theme="dark"] .issue-item,
[data-theme="dark"] .recommendation-item {
  background: var(--neutral-800);
  border-color: var(--neutral-600);
}

[data-theme="dark"] .issue-suggestion {
  background: var(--primary-900);
  border-color: var(--primary-700);
  color: var(--primary-200);
}

[data-theme="dark"] .rec-impact,
[data-theme="dark"] .rec-effort {
  background: var(--neutral-700);
}

[data-theme="dark"] .json-display {
  background: var(--neutral-900);
  color: var(--neutral-100);
}
